@import '../../../../src/styles/utils.scss';

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

li {
    list-style: none;
    margin: 0;
}

.TopRank {
    text-align: center;
    margin-left: 0;
    .high-rank {
        position: relative;
        display: inline-block;
        padding: 14px 18px;
        border: 2px solid #bdbbbb;
        width: 320px;
        background: #fff;
        box-sizing: border-box;
        text-align: left;
        margin-top: 30px;
        .Top_Rank {
            width: 30px;
            background: #bdbbbb;
            padding: 3px 0;
            line-height: 24px;
            font-family: Helvetica,AppleSDGothic,"Apple SD Gothic Neo",AppleGothic,Arial,Tahoma;
            font-size: 21px;
            text-align: center;
            color: #fff;
        }
        .Top_Name {
            display: inline-block;
            line-height: 24px;
            font-size: 20px;
            color: #242929;
            text-align: left;
        }
    }
    .First {
        display: block;
        width: 650px;
        margin: 0 auto;
        margin-top: 3rem;
        padding: 35px 40px;
        border-width: 5px;
    }
    .small {
        width: 220px;
        border-width: 5px;  
    }
    .small +  .small {
        margin-left: 2rem;
    }
}
