@import '../../../../src/styles/utils.scss';

.header {
    background: #000000;
    .header-content { 
        height: 7rem;
        width: 1400px;
        margin: 0 auto; // ì¤‘ì•™ ì •ë ¬
        padding-right: 3rem;
        
        // ë‚´ë¶€ ì•„ì´í…œ ì„¸ë¡œ ì¤‘ì•™ ì •ë ¬
        display: flex;
        align-items: center;
    
        // ë°˜ì‘í˜• ë ˆì´ì•„ì›ƒ
        @include media("<wide") {
          width: 100%;
        }
        @include media("<medium") {
          padding-left: 1rem;
          padding-right: 1rem;
        }
        
        .brand {
          img {
            width: 40%;
          }
        }
    
        .right {
          color: white;
          font-size: 1.2rem;
          font-weight: 600;
          display: flex;
          width: 50vw; 
          margin-left: auto;
          align-items: center;
          
          @include media("<large") {
            display: none;
          }
          
          a {
            &:hover {
              transition: all 0.3s ease-in;
              background-color: #201C29;
            }
            &:nth-child(5){
              border-right: 1px solid #201c29;
            }
            .item {
              display: flex; 
              width: 8rem;
              height: 7rem;
              border-left: 1px solid #201c29;
              padding: 0 1rem 0 1rem;
              align-items: center;
              justify-content: center;
              white-space: nowrap;  
            }
          }
          
          .login {
            padding: 10px 40px 10px 40px;
            border-radius: 30px;
            margin-left: 1rem;
            background: $oc-gray-7;
            cursor: pointer;
            &:hover {
              transition: all 0.3s ease-in;
              background: $oc-violet-3;
            }
          }
        }
      }
}